.user {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.user__icon {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-inline-end: 10px;
  margin-inline-start: auto;
}

.user__name {
  display: none;

  @media (min-width: $width-mobile-album) {
    display: flex;
    font-size: $text-mobile-font;
    line-height: $text-mobile-line;
    margin-inline-end: 10px;
  }
}

.user__popup {
  position: absolute;
  background-color: $white;
  box-shadow: 0px 10px 30px #0000000D;
  border: 1px solid #DDDDDD;
  border-radius: 15px;
  min-width: $width-mobile-container;
  width: calc(100% - 20px);
  height: 170px;
  left: 10px;
  top: 15px;
  display: none;
  flex-direction: column;
  justify-content: center;
  z-index: 12;

  @media (min-width: $width-mobile-album) {
    min-width: auto;
    max-width: 200px;
    height: 130px;
    left: auto;
    right: 15px;
    top: 80px;
  }

  @media (min-width: $width-desktop) {
    // right: 10%;
  }
}

.user__popup-header {
  display: flex;
  justify-content: space-between;

  @media (min-width: $width-mobile-album) {
    display: none;
  }
}

.user__popup-name {
  padding-inline-start: 30px;
  padding-inline-end: 15px;
  padding-block-start: 25px;
  padding-block-end: 15px;
}

.user__popup-close {
  padding-inline-start: 15px;
  padding-inline-end: 30px;
  padding-block-start: 25px;
  padding-block-end: 15px;
  cursor: pointer;
}

.user__popup-profile,
.user__popup-logout {
  display: block;
  padding-inline-start: 30px;
  padding-inline-end: 15px;
  padding-block-start: 15px;
  padding-block-end: 15px;
  color: $main-theme-color;
  text-decoration: underline;
}
