.apps {
  width: 100%;
  max-width: $width-desktop-container;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

.apps__list {
  list-style: none;
  padding-inline-start: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.apps__item {
  width: 45%;
  padding: 5px;

  @media (min-width: $width-tablet) {
    padding: 15px;
  }

  @media (min-width: $width-tablet-album) {
    width: 22%;
    padding: 15px;
  }
}

.apps__link {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 150px;
  background-color: $white;
  box-shadow: 0px 10px 30px #0000000D;
  border-radius: 15px;
  text-decoration: none;
}

.apps__link:hover,
.apps__link:focus,
.apps__link:active {
  box-shadow: 0px 10px 30px $main-border-color;
}

.apps__icon {
  height: 108px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.apps__tag {
  text-align: center;
  color: $link-text-color;
  font-weight: 400;
}

.apps__hr {
  margin-block-end: 0;
}